import { Component, useState, useEffect, useRef} from "react";
import { css } from "emotion";
import { Color } from "reactism/utils";
import { Wrapper } from "reactism/elements";
import { Link } from "Routes";
import { withRouter } from "next/router";

const HeaderTwo = ({ router }) => {

  const [ dropDown, setDropDown ] = useState(false)
  const wrapNav = useRef(null)

  useEffect(() => {
    document.addEventListener('click', handleDocumentClick , false);
    return () => {
      document.removeEventListener('click', handleDocumentClick , false);
    }
  })

  const handleDocumentClick  = e => {
    if (!(wrapNav.current).contains(e.target)) {
      setDropDown(false)
    }
  }

  const onClickBurger = () => {
    setDropDown(!dropDown);
    let show = document.getElementsByClassName('burgerButton');
    // show.classList.add('show');
    console.log(show)
  }

  const menuClick = () => {
    setDropDown(false)
  }

  const baseStyle = css`
      background-color: #fff;
      border-bottom: 2px solid rgba(0, 0, 0, 0.1);
      .burgerButton {
        display: none;
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 11;
        .bar1, .bar2, .bar3 {
          /* width: 25px;
          height: 3px;
          margin-bottom: 5px;
          background-color: rgba(0, 0, 0, 1); */
          width: 30px;
          height: 4px;
          background-color: #333;
          margin: 5px 0;
          transition: 0.4s;
        }
        &.show {
          .bar1 {
            -webkit-transform: rotate(-45deg) translate(-5px, 5px);
            transform: rotate(-45deg) translate(-5px, 5px);
          }
          .bar2 {
            opacity: 0;
          }
          .bar3 {
            -webkit-transform: rotate(45deg) translate(-8px, -8px);
            transform: rotate(45deg) translate(-8px, -8px);
          }
        }
      }
      .logo {
        display: none;
        position: absolute;
        width: 100%;
        top: -8px;
        left: 1em;
        z-index: 11;
        img {
          width: 40px;
        }
      }
      .wrapper {
        display: grid;
        grid-template-columns: 11.2% 11.2% 11.2% auto auto 12% 12% 12%;
        justify-items: center;
        margin: 0;
        padding: 0;
        a {
          text-decoration: none;
          display: block;
          padding: 0.8rem 0;
          color: #000;
          font-weight: bold;
          transition: 0.2s ease;
          &:hover {
            font-weight: bold;
            color: ${Color.success};
          }
        }

        li {
          list-style: none;
          margin: 0;
          transition: 0.2s ease;
          &.active a {
            color: ${Color.success};
            font-weight: bold;
            border-bottom: 3px solid ${Color.success};
          }
          &:hover:not(.active) {
            border-bottom: 3px solid ${Color.success};
          }
        }

        li:nth-child(1),
        li:nth-child(2),
        li:nth-child(3),
        li:nth-child(4) {
          justify-self: start;
        }
        li:nth-child(5),
        li:nth-child(6),
        li:nth-child(7),
        li:nth-child(8) {
          justify-self: end;
        }
      }

      @media (max-width: 700px) {
        background: #fff;
        height: 55px;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        /* height: 50px; */
        z-index: 10;
        .burgerButton {
          display: inline-block;
        }
        .logo {
          display: grid;
          grid-template-columns: 13% 1fr;
          align-items: center;
          width: 80%;
        }
        .wrapper {
          /* visibility: ${ dropDown }; */
          grid-template-columns: 1fr;
          position: absolute;
          display: block;
          background-color: #fff;
          padding-top: 50px;
          left: 0;
          right: 0;
          padding-left: 1em;
          padding-right: 1em;
          transition: 0.2s;
          li {
            transition: none;
            a {
              transition: none;
              padding: 1em; 
            }
            &.active a {
              border: none;
              color: #fff;
              background: ${Color.success};
            }
          } 
        }
      }
    `;

    const { pathname } = router;
    console.log(dropDown);
    return (
      <nav className={baseStyle} ref={ wrapNav }>
        <div className="burgerButton" onClick={ ()=> onClickBurger() }>
          <div className="bar1" />
          <div className="bar2" />
          <div className="bar3" />
        </div>
        <div className="logo">
          <img src="/static/logo_kejari_color.svg" alt="" />
          <h2>KEJARI BARRU</h2>
        </div>
        <Wrapper pl={7} pr={7}>
          <ul className={`${
          dropDown
            ? css`visibility: visible`
            : css`visibility: collapse`
          } wrapper`}>
            <li className={pathname === "/home" ? "active" : null} onClick={ () => menuClick() }>
              <Link route="/">
                <a>Beranda</a>
              </Link>
            </li>
            <li className={pathname === "/profile" ? "active" : null} onClick={ () => menuClick() }>
              <Link route="profil">
                <a>Profil</a>
              </Link>
            </li>
            <li className={pathname === "/tools" ? "active" : null} onClick={ () => menuClick() }>
              <Link route="sarana">
                <a>Sarana</a>
              </Link>
            </li>
            <li className={pathname === "/organization" ? "active" : null} onClick={ () => menuClick() }>
              <Link route="organisasi">
                <a>Organisasi</a>
              </Link>
            </li>
            <li className={ pathname === "/news" || pathname === "/detail" ? "active" : null } onClick={ () => menuClick() }>
              <Link route="berita">
                <a>Berita</a>
              </Link>
            </li>
            <li className={pathname === "/gallery" ? "active" : null} onClick={ () => menuClick() }>
              <Link route="galeri">
                <a>Galeri</a>
              </Link>
            </li>
            <li className={pathname === "/events" ? "active" : null} onClick={ () => menuClick() }>
              <Link route="kegiatan">
                <a>Kegiatan</a>
              </Link>
            </li>
            <li className={pathname === "/features" ? "active" : null} onClick={ () => menuClick() }>
              <Link route="layanan">
                <a>Layanan</a>
              </Link>
            </li>
          </ul>
        </Wrapper>
      </nav>
    );
  }

export default withRouter(HeaderTwo);
